<template>
    <div class="cart_container">
        <el-table ref="multipleTable" :data="goodsList" height="300" style="width: 100%" stripe
            @select-all="isFull = !isFull" @selection-change="handleSelectionChange">
            <!--  -->
            <el-table-column type="selection" width="55" :aria-checked="isFull"></el-table-column>
            <el-table-column align="center" label="" width="190">
                <template slot-scope="{ row }">
                    <el-image :src="row.image">
                        <div slot="error" class="image-slot">
                            <i class="el-icon-picture-outline"></i>
                        </div>
                    </el-image>
                </template>
            </el-table-column>
            <!--  -->
            <el-table-column align="center" label="" width="100" prop="name">
            </el-table-column>
            <!--  -->
            <!--  -->
            <el-table-column align="center" label="" width="140">
                <template slot-scope="{ row }">
                    {{ row.amount.toFixed(2) }}
                </template>
            </el-table-column>
            <!--  -->
            <el-table-column align="center" label="" width="140">
                <template slot-scope="{ row }">
                    <el-input-number v-model="row.quantity" size="mini" :min="1"
                        @change="updateNumber(row.productId, row.quantity)">
                    </el-input-number>
                </template>
            </el-table-column>
            <!--  -->
            <el-table-column align="center" label="" width="140">
                <template slot-scope="{ row }">
                    <el-button @click="onDeleteClick(row.productId)" type="text" size="small"></el-button>
                </template>
            </el-table-column>
        </el-table>
        <div class="footer-container">
            <el-checkbox :value="isFull" @change="onSelectAll"></el-checkbox>
            <el-button class="button" type="text" @click="onRemoveAll"></el-button>
            <div>
                <span></span>
                <span class="amount">{{ amount.toFixed(2) }}</span>
            </div>
            <el-button class="button" type="primary" round @click="onClickSubmit">{{ tatol }}</el-button>
        </div>
    </div>
</template>
  
<script>


export default {
    name: 'App',
    data() {
        return {
            goodsList: [],
            multipleSelection: [], //
            isFull: false
        }
    },
    computed: {
        amount() {
            let amount = 0
            this.multipleSelection.forEach((val) => {
                amount += val.amount * val.quantity
            })
            return amount
        },
        tatol() {
            let tatol = 0
            this.multipleSelection.forEach((val) => {
                tatol += val.quantity
            })
            return tatol
        },
    },
    created: function () {
        this.requestCart()
    },
    methods: {
        // 
        handleSelectionChange(val) {
            this.multipleSelection = val;
            console.log(val)
        },
        // 
        requestCart() {
            this.$api.cart.get()
                .then((response) => {
                    // console.log(response);
                    const res = response.data
                    if (res.code === 200) {
                        // this.$message.success("")
                        this.goodsList = res.data
                    } else {
                        this.$message.error(response.data.msg)
                    }
                })
        },
        // 
        onSelectAll() {
            this.$refs.multipleTable.toggleAllSelection()
        },
        // 
        updateNumber(productId, quantity) {
            // console.log(productId, quantity)
            this.$api.cart.updateNum(productId, quantity)
                .then((response) => {
                    // console.log(response);
                    const res = response.data
                    if (res.code === 200) {
                        this.$message.success('')
                    } else {
                        this.$message.error(res.msg)
                    }
                })
        },
        //
        onDeleteClick(productId) {
            this.updateNumber(productId, 0)
            //
            this.requestCart()
        },
        onClickSubmit() {

        },
        // 
        onRemoveAll() {
            this.$confirm(', ?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                this.removeAll()
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: ''
                });
            });
        },
        // 
        removeAll() {
            this.$api.cart.removeAll()
                .then((response) => {
                    // console.log(response);
                    const res = response.data
                    if (res.code === 200) {
                        this.$message.success('')
                        //
                        this.requestCart()
                    } else {
                        this.$message.error(res.msg)
                    }
                })
        }

    },

}
</script>
  
<style scoped>
.cart_container {
    padding-top: 45px;
    padding-bottom: 50px;
}

.footer-container {
    /* position: fixed; */
    /* bottom: 0; */
    height: 50px;
    width: 90%;
    z-index: 999;
    border-top: 1px solid #efefef;
    padding: 0 10px;
    background-color: white;

    display: flex;
    justify-content: space-between;
    align-items: center;
}

.amount {
    color: red;
    font-weight: bolder;
}
</style>
  