<template>
    <div>
        <el-page-header @back="goBack" content="" >
        </el-page-header>
        <el-form class="form_container" :model="ruleForm" :rules="rules" ref="ruleForm" label-width="80px">
            <el-row>
                <el-col :span="12">
                    <el-form-item label="" :label-width="formLabelWidth">
                        <el-input :placeholder="role" :disabled="true"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item label="ID" :label-width="formLabelWidth">
                        <el-input :placeholder="ruleForm.userId" :disabled="true"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="12">
                    <el-form-item label="" :label-width="formLabelWidth" prop="username">
                        <el-input v-model="ruleForm.username" placeholder="" autocomplete="off"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item label="" :label-width="formLabelWidth">
                        <el-input v-model="ruleForm.name" placeholder="" autocomplete="off"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="12">
                    <el-form-item label="" :label-width="formLabelWidth">
                        <el-select v-model="ruleForm.gender" placeholder="" style="width:80px">
                            <el-option v-for="item in genderOptions" :key="item.value" :label="item.label"
                                :value="item.value">
                            </el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item label="" :label-width="formLabelWidth">
                        <el-date-picker v-model="ruleForm.birthday" type="date" placeholder="" format="yyyy  MM  dd "
                            value-format="yyyy-MM-dd">
                        </el-date-picker>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-form-item label="" :label-width="formLabelWidth" prop="email">
                <el-input v-model="ruleForm.email" placeholder="" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="" :label-width="formLabelWidth" prop="phone">
                <el-input v-model="ruleForm.phone" placeholder="" autocomplete="off"></el-input>
            </el-form-item>
            <div style="margin-left: 70%;">
                <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
            </div>
        </el-form>
    </div>
</template>

<script>

export default {
    name: 'EditUserData',
    data() {
        return {
            genderOptions: [{
                value: '1',
                label: ''
            }, {
                value: '0',
                label: ''
            }, {
                value: '-1',
                label: ''
            }],
            role: "",
            ruleForm: {
                userId: 0,
                username: "",
                name: "",
                gender: "-1",
                password: '',
                birthday: "",
                role: "",
                phone: "",
                email: "",
            },
            formLabelWidth: '120px',
            rules: {
                username: [
                    { required: true, message: '', trigger: 'blur' },
                ],
                email: [
                    { type: 'email', message: '', trigger: ['blur', 'change'] }
                ],
                phone: [
                    { min: 11, max: 11, message: "11", trigger: ['blur', 'change'] }
                ]
            }
        }
    },
    created: function () {
        this.requestUserData()
    },
    methods: {
        goBack() {
            this.$router.go(-1)
        },
        getGenderStr(gender) {
            let genderStr = ""
            let genderOptions = [{
                value: '1',
                label: ''
            }, {
                value: '0',
                label: ''
            }, {
                value: '-1',
                label: ''
            }]
            genderOptions.forEach((obj) => {
                if (obj.value === gender) {
                    return genderStr = obj.label
                }
            })
            return genderStr
        },

        // 
        requestUserData() {
            // id
            let userId = this.$route.params.loginID
            // console.log(userId)
            this.$api.userInfo.get(userId)
                .then((response) => {
                    // console.log(response);
                    const res = response.data
                    if (res.code === 200) {
                        this.$message.success("")
                        this.ruleForm = res.data
                        this.genderStr = this.getGenderStr(res.data.gender)
                    } else {
                        this.$message.error(response.data.msg)
                    }
                })
        },
        submitForm(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    this.update()
                }
            })
        },
        update() {
            let data = this.ruleForm;
            // console.log(data)
            this.$api.userInfo.update(data)
                .then((response) => {
                    console.log(response);
                    //200
                    const res = response.data
                    if (res.code === 200) {
                        this.$message.success('')
                        //
                        this.onCancel()
                    } else {
                        this.$message.error(res.msg)
                    }
                })
        }
    },

}
</script>

<style lang="less" scoped>
.form_container {
    width: 95%;
    margin: 0 auto;
    margin-top: 50px;
}
</style>