<template>
    <div>
        <h3 style="font-size: 18px; margin-bottom: 5px;">{{ isAdd ? "" : "" }}</h3>
        <el-form class="form_container" :model="ruleForm" :rules="rules" ref="ruleForm" label-width="80px">
            <el-row>
                <el-col :span="8">
                    <el-form-item label="" :label-width="formLabelWidth" prop="recipientName">
                        <el-input v-model="ruleForm.recipientName" placeholder="" autocomplete="off"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item label="" :label-width="formLabelWidth" prop="recipientPhone">
                        <el-input v-model="ruleForm.recipientPhone" placeholder="" autocomplete="off"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="8">
                    <el-form-item label="" :label-width="formLabelWidth" prop="province">
                        <el-input v-model="ruleForm.province" placeholder=""></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="" :label-width="formLabelWidth" prop="city">
                        <el-input v-model="ruleForm.city" placeholder="" autocomplete="off"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="" :label-width="formLabelWidth" prop="district">
                        <el-input v-model="ruleForm.district" placeholder="" autocomplete="off"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-form-item label="" :label-width="formLabelWidth" prop="detail">
                <el-input v-model="ruleForm.detail" placeholder=""
                    autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item :label-width="formLabelWidth">
                <el-checkbox v-model="ruleForm.isDefault"></el-checkbox>
            </el-form-item>
            <div style="margin-left: 70%;">
                <div v-if="isAdd">
                    <el-button @click="clearRuleForm"></el-button>
                    <el-button type="primary" @click="addAddress('ruleForm')"></el-button>
                </div>
                <div v-else>
                    <el-button @click="onCancelEdit"></el-button>
                    <el-button type="primary" @click="editAddress('ruleForm')"></el-button>
                </div>

            </div>
        </el-form>
    </div>
</template>
<script>

export default {
    name: 'AddEditAddress.vue',
    props: {
        editData: {
            type: Object,
            default: () => {
                return {
                    "addressId": "",
                    "userId": "",
                    "recipientName": "",
                    "recipientPhone": "",
                    "province": "",
                    "city": "",
                    "district": "",
                    "detail": "",
                    "isDefault": false
                }
            }
        }
    },
    watch: {
        editData: function (val) {
            this.ruleForm = val
            // 
            this.isAdd = false
        },
        isAdd: function (isadd) {
            if (isadd) {
                this.clearRuleForm()
            }
        }

    },
    data() {
        return {
            isAdd: true,//truefalse
            ruleForm: this.editData,
            formLabelWidth: '120px',
            rules: {
                recipientName: [
                    { required: true, message: '', trigger: 'blur' },
                ],
                recipientPhone: [
                    { required: true, min: 11, max: 11, message: "11", trigger: ['blur', 'change'] }
                ],
                detail: [
                    { required: true, min: 5, message: '5', trigger: ['blur'] }
                ],
            }
        }
    },
    methods: {
        // 
        clearRuleForm() {
            this.ruleForm = {
                "addressId": "",
                "userId": "",
                "recipientName": "",
                "recipientPhone": "",
                "province": "",
                "city": "",
                "district": "",
                "detail": "",
                "isDefault": false
            }
        },
        // 
        onCancelEdit() {
            // 
            this.isAdd = true
            this.$emit('cancelEdit')
        },
        //
        addAddress(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    //1     
                    let isDefault = this.ruleForm.isDefault ? 1 : 0
                    let { recipientName, recipientPhone, province, city, district, detail } = this.ruleForm
                    let data = { recipientName, recipientPhone, province, city, district, detail, isDefault }
                    this.postAdd(data)
                }
            })
        },
        //
        postAdd(data) {
            // console.log(data)
            this.$api.address.add(data)
                .then((response) => {
                    console.log(response);
                    const res = response.data
                    if (res.code === 200) {
                        this.$message.success('')
                        //emit
                        this.$emit('addSuccess')
                    } else {
                        this.$message.error(res.msg)
                    }
                })
        },
        //
        editAddress(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    //1     
                    let isDefault = this.ruleForm.isDefault ? 1 : 0
                    let { addressId, recipientName, recipientPhone, province, city, district, detail } = this.ruleForm
                    let data = { addressId, recipientName, recipientPhone, province, city, district, detail, isDefault }
                    // console.log(data)
                    this.update(data)
                }
            })

        },
        //
        update(data) {
            // console.log(data)
            this.$api.address.update(data)
                .then((response) => {
                    // console.log(response);
                    const res = response.data
                    if (res.code === 200) {
                        this.$message.success('')
                        this.$emit('editSuccess')
                        // 
                        this.isAdd = true
                    } else {
                        this.$message.error(res.msg)
                    }
                })
        },
    },
}
</script>

<style lang="less" scoped>
.form_container {
    width: 95%;
    margin: 0 auto;
}
</style>