<template>
    <div class="users_container">
        <!--  -->
        <add-edit-address :edit-data="edmintData" @addSuccess="requestAddress"
            @editSuccess="requestAddress"></add-edit-address>
        <!--  -->
        <el-alert :title="''+ addressList.length +''" style="margin-top: 30px;" :closable="false" type="info" show-icon>
        </el-alert>
        <el-table :data="addressList" style="width: 100%; margin-top: 20px;" stripe border>
            <el-table-column prop="recipientName" align="center" label="" width="90">
            </el-table-column>
            <el-table-column align="center" label="" width="140">
                <template slot-scope="{ row }">
                    {{ row.province + " " + row.city }}
                    <br>
                    {{ row.district }}
                </template>
            </el-table-column>
            <el-table-column prop="detail" align="center" label="" width="200">
            </el-table-column>
            <el-table-column prop="recipientPhone" align="center" label="/" width="120">
            </el-table-column>
            <el-table-column align="center" label="" width="120">
                <template slot-scope="scope">
                    <el-button type="success" round plain size="small" @click="onEditClick(scope.$index)"></el-button>
                    <el-button @click="onDeleteClick(scope.row)" type="text" size="small"></el-button>
                </template>
            </el-table-column>
            <el-table-column align="center" label="">
                <template slot-scope="scope">
                    <el-tag v-if="scope.row.isDefault === 1" type="success"></el-tag>
                    <el-button v-else plain size="mini" @click="setDefault(scope.row)">
                        
                    </el-button>
                </template>
            </el-table-column>
        </el-table>
    </div>
</template>

<script>
import AddEditAddress from './AddEditAddress.vue'

export default {
    name: 'AddressTable',
    components: { AddEditAddress },
    props: {
        Addresslist: {
            type: Array
        }
    },
    data() {
        return {
            edmintData: {
                "addressId": "",
                "userId": "",
                "recipientName": "",
                "recipientPhone": "",
                "province": "",
                "city": "",
                "district": "",
                "detail": "",
                "isDefault": false
            },
            addressList: []
        }
    },
    created: function () {
        this.requestAddress()
    },
    methods: {
        onEditClick(index) {
            const item = this.addressList[index]
            // ,
            this.edmintData = { ...item }
            // isDefault
            let isDefault = this.edmintData.isDefault
            this.edmintData.isDefault = (isDefault === 1 ? true : false)
            //
            window.scrollTo(0, 0)
        },
        setDefault(row) {
            //1     
            let isDefault = 1
            let { addressId, recipientName, recipientPhone, province, city, district, detail } = row
            //
            let data = { addressId, recipientName, recipientPhone, province, city, district, detail, isDefault }
            this.update(data)
        },
        // 
        requestAddress() {
            this.$api.address.get()
                .then((response) => {
                    // console.log(response);
                    const res = response.data
                    if (res.code === 200) {
                        this.$message.success("")
                        this.addressList = res.data
                    } else {
                        this.$message.error(response.data.msg)
                    }
                })
        },
        //
        update(data) {
            // console.log(data)
            this.$api.address.update(data)
                .then((response) => {
                    console.log(response);
                    const res = response.data
                    if (res.code === 200) {
                        this.$message.success('')
                        //
                        this.requestAddress()
                    } else {
                        this.$message.error(res.msg)
                    }
                })
        },
        onDeleteClick(row) {
            // console.log(row.addressId)
            this.$confirm('?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                this.deleteAddress(row.addressId)
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: ''
                });
            });
        },
        deleteAddress(addressId) {
            this.$api.address.remove(addressId)
                .then((response) => {
                    // console.log(response);
                    const res = response.data
                    if (res.code === 200) {
                        this.$message.success("")
                        //
                        this.requestAddress()
                    } else {
                        this.$message.error(res.msg)
                    }
                })
        },
    },

}
</script>

<style lang="less" scoped></style>